import React from 'react'

const Etiqueta = () => {
  return (
    <div name='etiqueta' className='w-full lg:pt-30 bg-slate-100'>
      <div className='mx-auto'>
        <div className='text-center'>
            <h2 className='text-5xl font-bold py-10 text-center title text-teal-900'>Etiqueta</h2>
            <p className='md:text-2xl text-1xl py-6 text-gray-500 max-w-[700px] mx-auto pb-20 px-10'>Queremos que sea una fiesta informal en la que cada cual vaya como quiera.
            Nosotros nos pondremos bien guapos, con traje y corbata para la ceremonia, aunque probablemente nos cambiemos para bailar 
            despus ms cmodamente. As que sintete libre. Eso s, ten en cuenta que en Rascafra, al caer el sol, la temperatura baja casi siempre, incluso en junio. Es recomendable traerse algo por si refresca.</p>
        </div>       
      </div>
    </div>
  )
}

export default Etiqueta
